.Loader {
  animation: spin 1s linear infinite;

  border: 3px solid #000;
  border-top: 3px solid #fff;
  border-radius: 50%;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.small {
  width: 15px;
  height: 15px;
}

.medium {
  width: 20px;
  height: 20px;
}

.large {
  width: 25px;
  height: 25px;
}
